@import "mixins";
@import "admin-common";
@import "tools";

.button.image-choices-option-upload-button,
.image-choices-option-preview-wrap,
.image-choices-setting-title,
.image-choices-setting-use-images,
.image-choices-setting-theme,
.image-choices-setting-feature-color,
.image-choices-setting-align,
.image-choices-setting-image-style,
.image-choices-setting-columns,
.image-choices-setting-columns-medium,
.image-choices-setting-columns-small,
.image-choices-setting-height,
.image-choices-setting-height-medium,
.image-choices-setting-height-small,
.image-choices-setting-show-labels,
.image-choices-setting-use-lightbox,
.image-choices-setting-use-lightbox-caption,
.image-choices-setting-show-prices,
.image-choices-setting-layout,
.image-choices-setting-entry-value {
	display: none !important;
}
.image-choices-setting-columns-width,
.image-choices-setting-columns-width-medium,
.image-choices-setting-columns-width-small {
	display: none;
}

/* Both GF 2.5 + and GF < 2.5 */
#field_settings.image-choices-field-settings,
#field_settings_container.image-choices-field-settings,
#gfield_settings_choices_container.image-choices-field-settings,
#gquiz_gfield_settings_choices_container.image-choices-field-settings,
.toplevel_page_gf_edit_forms .image-choices-admin-field {

	.image-choices-setting-use-images {
		display: block !important;
	}

	.image-choices-enabled,
	&.image-choices-enabled {
		.button.image-choices-option-upload-button {
			display: inline-block !important;
		}
		.image-choices-has-image {
			.image-choices-option-preview-wrap {
				display: inline-block !important;
			}
			.image-choices-option-upload-button {
				display: none !important;
			}
		}
	}

	&.image-choices-use-images {

		&.image-choices-product-field .image-choices-setting-show-prices,
		.image-choices-setting-title,
		.image-choices-setting-theme,
		.image-choices-setting-feature-color,
		.image-choices-setting-align,
		.image-choices-setting-image-style,
		.image-choices-setting-columns,
		.image-choices-setting-columns-medium,
		.image-choices-setting-columns-small,
		.image-choices-setting-height,
		.image-choices-setting-height-medium,
		.image-choices-setting-height-small,
		.image-choices-setting-show-labels,
		.image-choices-setting-use-lightbox,
		.image-choices-setting-use-lightbox-caption,
		.image-choices-setting-layout,
		.image-choices-setting-entry-value {
			display: block !important;
		}

	}

}


/* GF 2.5+ */
#field_settings_container,
#gfield_settings_choices_container,
#gquiz_gfield_settings_choices_container {

	.image-choices-option-preview-wrap {
		margin: 0 8px 0 5px;
		padding: 3px;
	}
	.image-choices-option-preview {
		width: 30px;
		height: 30px;
	}
	.image-choices-option-image-remove {
		width: 20px;
		height: 20px;
		margin: -10px 0 0 -10px;
		i {
			line-height: 20px;
			font-size: 15px;
		}
	}

	.button.image-choices-option-upload-button {
		border-radius: 3px;
		font-size: 11px;
		//border: 1px solid #ffffff;
		border: 1px solid #DFDFDF;
		padding: 3px;
		margin: 0 8px 0 5px;
		line-height: 21px;
		vertical-align: middle;
		width: 36px;
		height: 36px;
	}

	&.image-choices-field-settings {

		#gfield_settings_choices_container,
		.gfield_settings_input_choices_container {
			max-height: none;
			margin-bottom: 20px;
		}

		.image-choices-enabled {

			.gfield_choice_header_label {
				padding-left: 50px;
			}
			.field-choice-input.field-choice-text {
				width: 180px !important
			}

			&.choice_with_value {
				.gfield_choice_header_value,
				.gfield_choice_header_label,
				.field-choice-input.field-choice-text,
				.field-choice-input.field-choice-value {
					width: 90px !important;
				}
				.gfield_choice_header_value {
					padding-left: 0;
				}
			}
			&.choice_with_price {
				.gfield_choice_header_label,
				.gfield_choice_header_price,
				.field-choice-input.field-choice-text,
				.field-choice-input.field-choice-price {
					width: 90px !important;
				}
				.gfield_choice_header_price {
					padding-left: 3px;
				}
			}
			&.choice_with_value_and_price {
				.gfield_choice_header_label,
				.field-choice-input.field-choice-text {
					width: 70px !important;
				}
				.gfield_choice_header_value,
				.field-choice-input.field-choice-value {
					width: 70px !important;
				}
				.gfield_choice_header_price,
				.field-choice-input.field-choice-price {
					width: 60px !important;
				}
				.gfield_choice_header_price {
					padding-left: 3px;
				}
			}

		}

	}

}


// new styles

.image-choices-admin-field {

	ul.gfield_radio,
	ul.gfield_checkbox,
	div.gfield_radio,
	div.gfield_checkbox {
		margin: 0;
		padding: 0;

		display: flex !important;
		flex-direction: row;
		flex-wrap: wrap;
		align-items: flex-start;
		justify-content: flex-start;
		grid-column-gap: 20px !important;
		grid-row-gap: 20px !important;
	}
	.gchoice_total {
		flex-basis: 100%;
	}

	.image-choices-choice {
		display: inline-block;
		margin: 0 !important;
		transition: none !important;
		border: none;
		background: transparent;
		vertical-align: top;
		text-align: center;
		position: relative;
		width: calc(20% - 20px);// shows max 5
		min-width: 100px;
		max-width: 200px;
		input[type="radio"],
		input[type="checkbox"] {
			@include accessibility;
		}
	}

	.image-choices-choice label,
	&.gppa-choices-enabled.image-choices-use-images .ginput_container .image-choices-choice label {
		text-indent: 0;

		cursor: pointer;
		display: inline-block;
		line-height: 0;
		margin: 0;
		background-color: transparent !important;
		width: 100% !important;
		height: auto !important;
		max-width: 100% !important;
		padding: 0;
		text-align: center;
		border: none;
	}

	.image-choices-choice-text,
	.image-choices-choice-price {
		font-size: 12px;
		font-weight: normal;
		line-height: 1.4;
		padding-top: 6px;
		transition: all 0.3s;
	}
	.image-choices-choice-price {
		display: block;
	}
	.image-choices-choice-text {
		display: none;
	}

	.image-choices-choice-image-wrap {
		display: inline-block;
		background-color: #efefef;
		background-repeat: no-repeat;
		background-position: 50%;
		background-size: cover;
		line-height: 0;
		width: 100%;
		//height: 150px;
		height: auto;
		padding-top: 100%;
		border-radius: 5px;
		text-align: center;
		transition: all 0.3s;
		position: relative;
	}
	.image-choices-choice-image-wrap:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		pointer-events: none;
		box-shadow: inset 0 0 0 0 #0076FF;
		z-index: 1;
		width: 100%;
		height: 100%;
		border-radius: 5px;
	}
	.image-choices-choice-selected .image-choices-choice-image-wrap:after,
	input:checked ~ label .image-choices-choice-image-wrap:after {
		box-shadow: inset 0 0 0 6px #0076FF;
	}


	.image-choices-choice-image {
		border-radius: 5px;
		display: none;
	}

	&.image-choices-show-labels {
		.image-choices-choice-text {
			display: block;
		}
	}

	.image-choices-choice .gftt-icon {
		right: 5px;
		top: 3px;
	}


	&.image-choices-use-lightbox .image-choices-choice label:before {
		content: "";
		display: block;
		width: 36px;
		height: 36px;
		border-radius: 50%;
		overflow: hidden;
		text-decoration: none;
		background-color: rgba(29, 29, 31, 0.8);
		background-image: url("data:image/svg+xml,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M2.80371 6.37645C2.80371 4.40326 4.40329 2.80368 6.37648 2.80368C8.34967 2.80368 9.94925 4.40326 9.94925 6.37645C9.94925 7.35107 9.559 8.23455 8.92623 8.87914C8.91816 8.8867 8.91019 8.89441 8.90231 8.90229C8.89444 8.91016 8.88673 8.91813 8.87917 8.9262C8.23458 9.55896 7.3511 9.94921 6.37648 9.94921C4.40329 9.94921 2.80371 8.34963 2.80371 6.37645ZM9.54631 10.9605C8.64643 11.5839 7.55411 11.9492 6.37648 11.9492C3.29873 11.9492 0.803711 9.4542 0.803711 6.37645C0.803711 3.29869 3.29873 0.80368 6.37648 0.80368C9.45424 0.80368 11.9493 3.29869 11.9493 6.37645C11.9493 7.55408 11.584 8.64641 10.9605 9.54629L12.9035 11.4892C13.294 11.8798 13.294 12.5129 12.9035 12.9034C12.5129 13.294 11.8798 13.294 11.4893 12.9034L9.54631 10.9605Z' fill='white'/%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		background-size: 16px;
		background-position: 50%;
		position: absolute;
		z-index: 3;
		top: 42%;
		left: 50%;
		transform: translate(-50%, -50%);
	}


}

.gf-image-choices-entry-choice {
	width: calc(20% - 20px);
	min-width: 100px;
	max-width: 200px;
	display: inline-block;
	margin: 0 20px 20px 0;
}
.gpoll_entry .gf-image-choices-entry-choice {
	width: calc(20% - 26px);
}
.gf-image-choices-entry-choice-image {
	display: block;
	width: 100%;
	height: auto;
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: cover;
	margin-bottom: 10px;
	&[style*="background-image:"] {
		border-radius: 5px;
		padding-top: 100%;
		img {
			display: none;
		}
	}
	img {
		display: block;
		width: 100% !important;
		height: auto;
		border-radius: 5px;
	}
}
.gf-image-choices-entry-choice-text {
	display: block;
	text-align: center;
}
