#image_choices_url_replacement,
#image_choices_image_replacement {
	.jetbase-tool__footer {
		text-align: right;
	}
	&.disabled {
		pointer-events: none !important;
		opacity: 0.3;
	}
}

.jetbase-tool__progress {
	padding: 20px;
	margin: 20px 0;
	border: 1px solid #d5d7e9;
	border-radius: 3px;
	box-shadow: 0 2px 1px rgba(28, 31, 63, 0.0634624);
}
.jetbase-tool__progress-status {
	margin-bottom: 10px;
}
.jetbase-tool__progress-bar {
	background-color: #F8F8F9;
	border-radius: 15px;
	width: 100%;
	height: 30px;
	overflow: hidden;
	position: relative;
}
.jetbase-tool__progress-percent {
	background-color: #4e7ca2;
	width: 0;
	height: 30px;
	position: absolute;
	left: 0;
	top: 0;
	will-change: width;
	transition: width 0.3s ease-in-out;
}
.jetbase-spinner {
	width: 36px;
	height: 36px;
	background-image: url(../images/spinner.svg);
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: contain;
	display: inline-block;
}
.jetbase-tool__progress:not(.active) {
	display: none;
}
.jetbase-tool:not(.busy) .jetbase-spinner {
	display: none;
}

.jetbase-tool.busy {
	.jetbase-tool__body > *:not(.jetbase-tool__progress) {
		pointer-events: none;
		opacity: 0.5;
	}
	.jetbase-tool__footer button {
		pointer-events: none;
		display: none;
	}
}
